<template>
	<el-dropdown trigger="click">
		<!-- <img class="head-portrait" :src="userInfo.img"/> -->
		<div class="user-info">
			<el-avatar class="head-portrait" shape="circle" :size="28" fit="fill" :src="getUserInfo.userImg"></el-avatar>
			<span class="user-info__name">{{getUserInfo.userName}}</span>
			<icon icon="gd-arrow-down"></icon>
		</div>
		<el-dropdown-menu slot="dropdown" class="ui-dropdown">
			<el-dropdown-item @click.native='pathClick'>个人中心</el-dropdown-item>
			<el-dropdown-item>消息</el-dropdown-item>
			<el-dropdown-item>设置</el-dropdown-item>
			<el-dropdown-item divided @click.native="logout">退出</el-dropdown-item>
		</el-dropdown-menu>
	</el-dropdown>
</template>

<script>
	export default {
		name: 'UserInfo',
		methods: {
			pathClick() {
				this.$router.push('/profile')
			},
			logout() {
				localStorage.removeItem('token');
				localStorage.removeItem('userInfo');
				this.$router.replace('/user/login');
			},
		},
		computed: {
			getUserInfo() {
				return this.$store.getters['user/getuserInfo']
			}
		}
		
	}
</script>

<style>
	.el-popper .popper__arrow::after {
		content: none;
	}
	.user-info{
		height: 100%;
	}
	.head-portrait {
		vertical-align: middle;
		margin-right: 10px;
	}
	.user-info__name {
		margin-right: 5px;
		color: #fff;
	}
	.ui-dropdown {
		margin: 0 !important;
	}
</style>
